
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components: {
XHeader,
InlineLoading
},
data() {
return {
renzheng: '',
isAssociated: false,
}
},
mounted() {
this.renzheng = this.$root.loginInfo.CSMZT;
this.selectInforMation();
},
methods: {
starActivity(index) {
if (index == 1) { //实名认证
if (this.renzheng == 1) { //失败
this.$router.push({
name: 'AuditFailure'
})
} else if (this.renzheng == 2) { //成功
this.$router.push({
name: 'Authentication'
})
} else if (this.renzheng == 3) { //认证中
this.$router.push({
name: 'uploadDone'
})
} else if (this.renzheng == 4) { //未认证
this.$router.push('/RealNameSystem')
}
} else if (index == 2) { //修改密码
this.$router.push({
name: 'changePWD'
})
} else if (index == 3) { //修改档案密码
let that = this;
if (this.isAssociated){

this.$vux.confirm.show({
title: '是否记得档案密码',
cancelText: '不记得',
confirmText: '记得',
onCancel() {
that.$router.push('/ForgetPWD');
},
onConfirm() {
that.$router.push('/changeFilePWD');
}
})
} else {

if (this.renzheng == 1) {
this.$vux.confirm.show({
title: '实名认证失败，实名认证成功才能修改档案密码',
confirmText: '重新认证',
onCancel() {},
onConfirm() {
that.$router.push('/RealNameSystem');
}
})
} else if (this.renzheng == 2) {

this.$vux.confirm.show({
title: '需要先关联健康档案才可以修改档案密码',
confirmText: '去关联',
onCancel() {},
onConfirm() {
that.$router.push('/AssociatedFiles');
}
})
} else if (this.renzheng == 3) {
this.$vux.confirm.show({
title: '实名认证中，实名认证成功才能修改档案密码',
showCancelButton: false,
})
} else {

this.$vux.confirm.show({
title: '未实名认证，实名认证成功才能修改档案密码',
confirmText: '去认证',
onCancel() {},
onConfirm() {
that.$router.push('/RealNameSystem');
}
})
}
}
}
},

selectInforMation(){
this.$ajax.post('',{
'MSH.1':'healthFileAssociateService',
'MSH.2':'isAssociated',
'CHZBM':this.$root.loginInfo.CHZBM,
'token':this.$root.loginInfo.token,
}).then(data => {
if(typeof data !='undefined'){
this.isAssociated = data.status;
}
}).catch(error => {
console.log(error);
});
},
}
}



